import Head from 'next/head'
import linkDataGen from '../components/linkDataGen'
import Links from '../components/Links'
import MyNav from "../components/MyNav"

export default function Home() {

  const linkComponents = linkDataGen.map(link => <Links key={link.id} site={link.site} url={link.url} />)
  return (
    <div className="container">
      <Head>
        <title>Course Services Links</title>
      </Head>

      <main>
        <MyNav />
        <h1 className="title text-center">
          Course Services Resources
        </h1>


        <h1>Useful Links</h1>
        {linkComponents}
        <Links />
      </main>
      <footer>
        Powered by ReactJs and Next.js
      </footer>

    </div>
  )
}
